import React, { useState } from "react";
import { FaBars, FaTimes, FaGithub, FaInstagram } from "react-icons/fa";
import { motion } from "framer-motion";
import data from "../navData";

const Sidebar = () => {
  const [isShow, setIsShow] = useState(false);
  const [word, setWord] = useState("");
  const variants = {
    hidden: {
      clipPath: "circle(20px at 50% -30% )",
      transition: {
        type: "spring",
        stiffness: 50,
        duration: 3,
      },
    },
    visible: {
      clipPath: "circle(1500px at 50% 50%)",
      transition: {
        type: "spring",
        duration: 3,
      },
    },
  };

  const childrenVariants = {
    hidden: {
      y: -50,
      opacity: 0,
    },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        delay: 0.3,
      },
    },
    hover: {
      scale: 1.1,
    },
  };
  return (
    <div className="sidebar">
      <div className="nav-left">
        <motion.a
          href="#"
          className="nav-name"
          initial={{ y: -100, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.8, type: "tween" }}
        >
          <div className="img-container">
            <img className="photo" src="me.jpg" alt="Simon Villaverde" />
          </div>
          <span>Simon Villaverde</span>
        </motion.a>
        <motion.button
          className="nav-btn"
          onClick={() => setIsShow(!isShow)}
          initial={{ y: -100, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.8, type: "tween" }}
        >
          {!isShow ? <FaBars /> : null}
        </motion.button>
      </div>

      <motion.div
        className="nav-items-container"
        variants={variants}
        initial={false}
        animate={isShow ? "visible" : "hidden"}
      >
        <ul className="sidebar-items active">
          {data.map(({ path, name, id }) => {
            return (
              <motion.li
                onMouseMove={(e) => setWord(e.target.innerHTML)}
                key={id}
                variants={childrenVariants}
              >
                <a href={path}>{name}</a>
              </motion.li>
            );
          })}
          <div className="sidebar-icons">
            <a href="https://github.com/simonn435" className="github">
              <FaGithub />
            </a>
            <a className="instagram">
              <FaInstagram />
            </a>
          </div>
        </ul>
        <button className="nav-modal-btn" onClick={() => setIsShow(false)}>
          {isShow ? <FaTimes /> : null}
        </button>
        <h3 className="word">{word}</h3>
      </motion.div>
    </div>
  );
};

export default Sidebar;
