import React from "react";
import { FaReact, FaJsSquare, FaCss3, FaHtml5 } from "react-icons/fa";
import { motion } from "framer-motion";

const SkillsIcons = ({ isVisible, setVal, info }) => {
  return (
    <div className="skills-container">
      <motion.div
        className="skills-html"
        animate={{ opacity: isVisible ? 1 : 0, y: isVisible ? 0 : 100 }}
        transition={{ delay: 0.1, type: "tween", duration: 0.8 }}
        onClick={() => {
          setVal(info[1].id);
        }}
      >
        <FaHtml5 className="skills-icon" />
      </motion.div>
      <motion.div
        className="skills-css"
        animate={{ opacity: isVisible ? 1 : 0, y: isVisible ? 0 : 100 }}
        transition={{ delay: 0.2, type: "tween", duration: 0.8 }}
        onClick={() => {
          setVal(info[2].id);
        }}
      >
        <FaCss3 className="skills-icon" />
      </motion.div>
      <motion.div
        className="skills-html"
        animate={{ opacity: isVisible ? 1 : 0, y: isVisible ? 0 : 100 }}
        transition={{ delay: 0.3, type: "tween", duration: 0.8 }}
        onClick={() => {
          setVal(info[3].id);
        }}
      >
        <FaJsSquare className="skills-icon" />
      </motion.div>
      <motion.div
        className="skills-css"
        animate={{ opacity: isVisible ? 1 : 0, y: isVisible ? 0 : 100 }}
        transition={{ delay: 0.4, type: "tween", duration: 0.8 }}
        onClick={() => {
          setVal(info[4].id);
        }}
      >
        <FaReact className="skills-icon" />
      </motion.div>
    </div>
  );
};

export default SkillsIcons;
