import React, { useState } from "react";
import { useViewportScroll, motion, useTransform } from "framer-motion";
import { FaInstagram, FaGithub } from "react-icons/fa";
import { useEffect } from "react";

const About = () => {
  const { scrollY } = useViewportScroll();
  const y = useTransform(scrollY, [200, 1000], [150, -50]);
  const [show, setShow] = useState(false);

  useEffect(() => {
    const scrollH = () => {
      if (scrollY.current > 300) {
        setShow(true);
      } else {
        setShow(false);
      }
    };
    window.addEventListener("scroll", scrollH);
    return () => {
      window.removeEventListener("scroll", scrollH);
    };
  });

  const abVar = {
    hidden: {
      y: 200,
      opacity: 0,
    },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        type: "tween",
        duration: 1,
      },
    },
  };

  return (
    <section className="about container">
      <motion.div
        className="about-content"
        variants={abVar}
        animate={show ? "visible" : "hidden"}
      >
        <h2 className="about-title">About</h2>
        <h3>Who i am?</h3>
        <p>
          I am a boy who wants to be a good web developer studying in a
          self-taught way. I started programming in C just as a hobby and I
          liked it, so I did more research and discovered web development.
        </p>
        <span>Checkout my social media</span>
        <div className="about-icons">
          <a href="#">
            <FaInstagram className="icon" />
          </a>
          <a href="#">
            <FaGithub className="icon" />
          </a>
        </div>
        <p className="about-contact">Contact me!</p>
        <b className="about-email">
          Email: <span> simonvillaverde9@gmail.com</span>
        </b>
      </motion.div>
      <div className="about-img">
        <motion.img src="me.jpg" alt="Simon Villaverde" style={{ y: y }} />
      </div>
    </section>
  );
};

export default About;
