import React, { useState, useEffect } from "react";
import {
  motion,
  AnimatePresence,
  useViewportScroll,
  useTransform,
} from "framer-motion";

const Header = () => {
  const [number, setNumber] = useState(0);
  const { scrollY } = useViewportScroll();

  const y = useTransform(scrollY, [200, 600], [0, 150]);
  const opacity = useTransform(scrollY, [200, 600], [1, 0]);

  const words = [
    <h1 className="title">
      <span className="text-gray">Hi there! I'm</span>
      <br />
      <b>Simon Villaverde</b>
    </h1>,
    <h1 className="title">
      <span className="text-gray">
        I'm a
        <br />
      </span>
      <b>Web Developer</b>
    </h1>,
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      if (number === words.length - 1) {
        return setNumber(0);
      } else {
        setNumber(number.length - 1);
      }
      setNumber(number + 1);
    }, 4000);
    return () => {
      clearInterval(interval);
    };
  }, [number, words.length]);

  return (
    <motion.header
      className="header container"
      initial={{ y: -100, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.8, type: "tween" }}
      style={{ y: y, opacity: opacity }}
    >
      <AnimatePresence initial={false}>
        <motion.div
          className="header-title"
          key={number}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          transition={{
            type: "tween",
            duration: 0.8,
          }}
        >
          {words[number]}
        </motion.div>
      </AnimatePresence>
    </motion.header>
  );
};

export default Header;
