import React from "react";
import { motion, AnimatePresence } from "framer-motion";
import { FaCheck } from "react-icons/fa";
import { useEffect } from "react";

const SkillsTab = ({ info, value }) => {
  const { text, title, id } = info[value];

  const titleVar = {
    hidden: {
      opacity: 0,
      y: -50,
    },
    visible: {
      opacity: 1,
      y: 0,
    },
    exit: {
      opacity: 1,
      y: -50,
    },
  };

  const ulvar = {
    animate: {
      transition: { duration: 1, staggerChildren: 0.2 },
    },
  };

  const livar = {
    initial: { x: -200, opacity: 0 },
    animate: { x: 0, opacity: 1, transition: { duration: 0.5 } },
  };

  return (
    <AnimatePresence>
      <div className="skills-tab">
        <motion.h3
          key={id}
          variants={titleVar}
          initial="hidden"
          animate="visible"
        >
          {title}
        </motion.h3>
        <motion.ul
          key={title}
          variants={ulvar}
          initial="initial"
          animate="animate"
        >
          {text.map((content, index) => {
            return (
              <motion.li key={index} variants={livar}>
                <FaCheck className="skills-tab-icon" />
                <p>{content}</p>
              </motion.li>
            );
          })}
        </motion.ul>
      </div>
    </AnimatePresence>
  );
};

export default SkillsTab;
