import React from "react";
import { useViewportScroll, motion } from "framer-motion";
import { useState, useEffect } from "react";
import SkillsTab from "./SkillsTab";
import SkillsIcons from "./SkillsIcons";
import { info } from "../../SkillsInfo";

const Skills = () => {
  const { scrollY } = useViewportScroll();
  const [isVisible, setIsVisible] = useState(false);
  const [value, setValue] = useState(0);

  const contVar = {
    hidden: {
      opacity: 0,
      y: 100,
    },
    visible: {
      opacity: 1,
      y: 0,
    },
  };

  const setVal = (i) => {
    setValue(i);
  };

  useEffect(() => {
    const scrollHandler = () => {
      if (scrollY.current > 800) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    };
    window.addEventListener("scroll", scrollHandler);
    return () => {
      window.removeEventListener("scroll", scrollHandler);
    };
  });

  return (
    <motion.section
      className="skills container"
      variants={contVar}
      animate={isVisible ? "visible" : "hidden"}
      transition={{ when: "beforeChildren", duration: 1 }}
    >
      <div className="skills-header">
        <h2 className="skills-title">Skills</h2>
        <h3 className="skills-subtitle">What can i help you with?</h3>
      </div>
      <SkillsIcons isVisible={isVisible} info={info} setVal={setVal} />
      <SkillsTab info={info} value={value} />
    </motion.section>
  );
};

export default Skills;
